

$(document).ready(function () {
  var blitzData = [];
  init();
  function init() {
    $('.difficulty input[type=radio]').first().attr('checked', 'checked');
    $.getJSON('/db/misc/gb-info.json', function(data){
      blitzData = data;
      var quests = getQuests(blitzData[0].Difficulty);
      var tickets = getTicketsCost(blitzData[0].Difficulty);
      updateOpponents(quests, 1, quests.length, tickets);
    })
    .fail(function() {
      alert("error");
    });
  }

  function getQuests(difficulty) {
    if (!blitzData || blitzData.length <= 0)
      return "Error!";

    difficulty = difficulty.toLowerCase();
    for (var i in blitzData) {
      var thisBlitzData = blitzData[i];
      if (thisBlitzData.Difficulty.toLowerCase() == difficulty) {
        return thisBlitzData.Quests;
      }
    }
  }

  function getTicketsCost(difficulty) {
    if (!blitzData || blitzData.length <= 0)
      return "Error!";

    difficulty = difficulty.toLowerCase();
    for (var i in blitzData) {
      var thisBlitzData = blitzData[i];

      if (thisBlitzData.Difficulty.toLowerCase() == difficulty) {

        return thisBlitzData.TicketCost;
      }
    }
  }

  function rewardExists(rewards, rewardID) {
    for (var i in rewards) {
      var thisReward = rewards[i];
      if (thisReward.ID == rewardID) {
        return true;
      }
    }
    return false;
  }

  function getRewards(quests, lastOpponent) {
    var rewards = [];

    for (var i in quests) {
      if (i == lastOpponent) {
        break;
      }
      var thisQuest = quests[i];
      var questReward = jQuery.extend({}, thisQuest.Reward);
      var existingReward = rewardExists(rewards, questReward.ID);
      if (!existingReward) {
        rewards.push(questReward);
      } else {
        for (var j in rewards) {
          if (rewards.hasOwnProperty(j)) {
            if (rewards[j].ID == questReward.ID) {
              rewards[j].Amount = parseInt(rewards[j].Amount) + parseInt(questReward.Amount);
            }
          }
        }
      }
    }

    return rewards;
  }

  function updateOpponents(quests, totalRuns, opponentsNumber, tickets) {
    var rewards = getRewards(quests, opponentsNumber);

    $('#ticketCost').empty();
    $('#rewardsTable tbody').empty();
    $('#opponentsReferenceTable tbody').empty();

    $('#ticketCost').text(parseInt(tickets) * totalRuns);
    $('input[name="lastOpponent"]').val(opponentsNumber);

    rewards.forEach(function(reward){
      $('#rewardsTable tbody').append('<tr><td>' + reward.Reward + '</td><td>' + (parseInt(reward.Amount) * totalRuns) + '</td></tr>')
    });

    quests.forEach(function(quest){
      $('#opponentsReferenceTable tbody').append('<tr><td>' + quest.QuestID + '</td><td>' + quest.Name + '</td><td>' + quest.Reward.Reward + ' x ' + quest.Reward.Amount + '</td></tr>')
    });
  }

  $('.difficulty input[type=radio]').on('change', function() {
    var totalRuns = $('input[name="totalRuns"]').val();
    var quests = getQuests(this.value);
    var tickets = getTicketsCost(this.value);
    updateOpponents(quests, totalRuns, quests.length, tickets);
  });

  $('#decreaseTotalRuns').on('click', function() {
    var currentDifficulty = $('.difficulty input[type=radio]:checked').val();
    var currentOpponentsNumber = $('input[name="lastOpponent"]').val();
    var currentNumber = $('input[name="totalRuns"]').val();
    if (isNaN(currentNumber) || currentNumber <= 1) {
      return;
    }
    var newValue = parseInt(currentNumber) - 1;
    $('input[name="totalRuns"]').val(newValue);
    var quests = getQuests(currentDifficulty);
    var tickets = getTicketsCost(currentDifficulty);
    updateOpponents(quests, newValue, currentOpponentsNumber, tickets);
  });

  $('#increaseTotalRuns').on('click', function() {
    var currentDifficulty = $('.difficulty input[type=radio]:checked').val();
    var currentOpponentsNumber = $('input[name="lastOpponent"]').val();
    var currentNumber = $('input[name="totalRuns"]').val();
    if (isNaN(currentNumber)) {
      return;
    }
    var newValue = parseInt(currentNumber) + 1;
    $('input[name="totalRuns"]').val(newValue);
    var quests = getQuests(currentDifficulty);
    var tickets = getTicketsCost(currentDifficulty);
    updateOpponents(quests, newValue, currentOpponentsNumber, tickets);
  });

  $('#decreaseLastOpponent').on('click', function() {
    var currentNumber = $('input[name="lastOpponent"]').val();
    var currentDifficulty = $('.difficulty input[type=radio]:checked').val();
    var currentRuns = $('input[name="totalRuns"]').val();

    if (isNaN(currentNumber) || currentNumber <= 1) {
      return;
    }

    var newValue = parseInt(currentNumber) - 1;
    var quests = getQuests(currentDifficulty);
    var tickets = getTicketsCost(currentDifficulty);
    updateOpponents(quests, currentRuns, newValue, tickets);
  });

  $('#increaseLastOpponent').on('click', function() {
    var currentNumber = $('input[name="lastOpponent"]').val();
    var currentDifficulty = $('.difficulty input[type=radio]:checked').val();
    var currentRuns = $('input[name="totalRuns"]').val();
    var quests = getQuests(currentDifficulty);
    var tickets = getTicketsCost(currentDifficulty);

    if (isNaN(currentNumber) || currentNumber >= quests.length) {
      return;
    }
    var newValue = parseInt(currentNumber) + 1;
    updateOpponents(quests, currentRuns, newValue, tickets);
  });

});



